.modal {    
    
    position: absolute;
    right: 0;
    /* z-index: 1; */
    opacity: 0;
    pointer-events: none;
    transition: 0.4s;
    width: 272px;
    top: 10px;
}

.modal.actiive {
    opacity: 1;  
    pointer-events: all;
    /* width: 272px; */
    top: 42px;
    width: 100vw;
    height: 100vh;
}

.modal__content {
    padding: 24px;     
    border-radius: 20px;
    background: #1D1D1D;
    text-align: left;
    width: 272px;
    /* transform: scale(0.5); */
    transition: 0.4s;

    right: 0;
    position: absolute;
    top: 35px;

}


.modal__content {
    box-sizing: border-box;
}

.modal__content__left {
    display: flex;
    flex-direction: column;
    gap: 24px;
}


.modal__content__left__item {
    display: flex;
    align-items: center;
    gap: 8px;

    font-size: 18px;
    font-family: 'Manrope', sans-serif;
    font-style: normal;
    font-weight: 700;

    cursor: pointer;   

    outline: none;
    background: none;
    border: none;
}

.modal_wrapper {
    position: absolute;;
}


.invisible_item {
    position: absolute;
    width: 100vw;
    height: 100vh;
}




.dropdown {    
    position: absolute;
    right: 0;
    /* z-index: 1; */
    opacity: 0;
    /* display: none; */
    pointer-events: none;
    transition: 0.4s;
    width: 216px;
    top: 10px;
    box-sizing: border-box;
}

.dropdown.actiive {
    opacity: 1;  
    pointer-events: all;
    /* width: 272px; */
    top: 42px;
    z-index: 1;
    width: 100vw;
    height: 100vh;
}

.dropdown__content {
    border-radius: 50px;

    padding: 12px 24px;

    box-sizing: border-box;
    background: #1D1D1D;
    text-align: left;
    width: 216px;
    /* transform: scale(0.5); */
    transition: 0.4s;

    right: 0;
    position: absolute;
    top: 40px;

    color: #FFF;

    font-size: 18px;
    font-family: Manrope;
    font-style: normal;
    font-weight: 700;
    line-height: 24px;

}


.dropdown__content {
    box-sizing: border-box;
}

.dropdown__content__left {
    display: flex;
    flex-direction: column;
    gap: 24px;
}

.dropdown__content__left__disconect_btn {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 8px;
    cursor: pointer;
}

.dropdown__content__left__item {
    display: flex;
    align-items: center;
    gap: 8px;

    font-size: 18px;
    font-family: 'Manrope', sans-serif;
    font-style: normal;
    font-weight: 700;

    cursor: pointer;   

    outline: none;
    background: none;
    border: none;
}

.dropdown_wrapper {
    position: absolute;
}
