.connect-btn {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    gap: 10px;
    
    z-index: 2;

    min-width: 189px;
    height: 48px;

    background: linear-gradient(267.74deg, #FFC700 5.99%, #E43086 114.38%);
    box-shadow: 0px 0px 10px rgba(244, 100, 53, 4.35);
    border-radius: 50px;
    border: none;
    
    font-family: 'Manrope', sans-serif;
    font-style: normal;
    
    font-size: 18px;
    line-height: 25px;
    color: #FFFFFF;
    cursor: pointer;

    color: #FFF;
    text-align: right;
    font-size: 18px;
    font-family: Manrope;
    font-weight: 700;

}

.connect-btn:hover {
    background: linear-gradient(267.74deg, #ecce5f 5.99%, #E43086 114.38%);
    /* box-shadow: 0px 0px 20px rgba(244, 100, 53, 4.35); */
}

.connect-btn:active {
    background: linear-gradient(267.74deg, #ecce5f 5.99%, #E43086 114.38%);
}