.modal {    
    
    position: absolute;
    right: 0;
    /* z-index: 1; */
    opacity: 0;
    pointer-events: none;
    transition: 0.4s;
    width: 272px;
    top: 10px;
    box-sizing: border-box;
}

.modal.actiive {
    opacity: 1;  
    pointer-events: all;
    /* width: 272px; */
    top: 42px;
    width: 100vw;
    height: 100vh;
    z-index: 1;
    box-sizing: border-box;
}

.modal__content {
    padding: 24px;     
    border-radius: 20px;
    background: #1D1D1D;
    text-align: left;
    width: 272px;
    /* transform: scale(0.5); */
    transition: 0.4s;

    right: 0;
    position: absolute;
    top: 35px;

}


.modal__content {
    box-sizing: border-box;
}

.modal__content__left {
    display: flex;
    flex-direction: column;
    gap: 24px;
}


.modal__content__left__item {
    display: flex;
    align-items: center;
    gap: 8px;

    font-size: 18px;
    font-family: 'Manrope', sans-serif;
    font-style: normal;
    font-weight: 700;

    cursor: pointer;   

    outline: none;
    background: none;
    border: none;
    padding: 0;
}

.modal_wrapper {
    position: absolute;;
}


.invisible_item {
    position: absolute;
    width: 100vw;
    height: 100vh;
}




