@import "normalizez.scss"
@import "resets.scss"
@import "fonts/stylesheet.css"
@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@300;400;500;600;700;800&display=swap')
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;700&display=swap')
    
[class*="__container"]
    max-width: 1450px
    margin: 0 auto
    padding: 0 15px

html
    box-sizing: border-box

body
    text-align: center
    box-sizing: border-box
    font-family: 'Manrope', sans-serif
    user-select: none
    height: 100vh
    // overflow: hidden

body:focus
    color: red
    border-color: red
    outline-color: red
.desctope
    display: initial
a
    text-decoration: none

div
    margin: 0
    padding: 0px

.orange
    color: #FF9F2E


:root
    --logo-size:80px
    --logo-half-size:40px

p
    color: #fff

.img
    user-select: none

.balance_row
    display: flex
    align-items: center
    gap: 16px
    &__chain_type_btn
        display: flex
        height: 51px
        padding: 0px 16px
        justify-content: center
        align-items: center
        gap: 8px

        border-radius: 57px
        border: 1px solid #1F1F1F
        cursor: pointer

        color: #FFF
        font-size: 18px
        font-family: Manrope
        font-style: normal
        font-weight: 700
        line-height: normal

        

.balance_eth__drop_down
    display: none

.loader-container 
    width: 100%
    height: 100vh
    display: flex
    justify-content: center
    align-items: center
    position: fixed
    background: #000000 url("components/Preloader.gif") center no-repeat
    z-index: 10
    transition: background 5s ease

.delAfter
    width: 424px
    height: 6000px
    background: url("photo_2023-06-08_17-50-05.jpg") no-repeat
    text-align: center
    margin: 0 auto

.active
    border-bottom: solid 2px #FF9F2E
    color: #FF9F2E !important

.conn
    background: #1D1D1D !important
    box-shadow: none !important
    transition: 0.3s all

    &:hover
        background: #252525 !important

.eth_img
    background: url('components/eth.png') no-repeat
    width: 21px
    height: 21px

.balance_row__eth
    display: flex
    align-items: center
    gap: 8px

    height: 48px
    cursor: pointer
    &:hover > .balance_eth__drop_down
        display: flex !important


.hedaer__row__inner
    display: flex
    align-items: center
    justify-content: center
    gap: 77px

.header
    background-color: #000
    border-bottom: solid 2px rgba(40, 40, 40, 0.5)
    margin-bottom: 48px
    &__row
        display: flex
        position: relative
        justify-content: space-between
        align-items: center
        height: 88px           
    &__nav_row
        display: flex
        gap: 30px
        font-size: 18px
        font-family: sans-serif
        &__connect-btn
            display: flex
            flex-direction: row
            justify-content: center
            align-items: center

            gap: 5px
            
            padding: 12px 24px
            // width: 216px
            height: 48px

            background: linear-gradient(267.74deg, #FFC700 5.99%, #E43086 114.38%)
            box-shadow: 0px 0px 10px rgba(244, 100, 53, 4.35)
            border-radius: 50px
            border: none

            // transition: 0.3s all
            
            font-family: 'Manrope', sans-serif
            font-style: normal
            font-weight: 700
            font-size: 18px
            line-height: 25px

            color: #FFFFFF

            cursor: pointer

            box-sizing: border-box
        // &__connect-btn:active
           

            color: #fff
        &__connect-btn:hover
            background: linear-gradient(267.74deg, #ecce5f 5.99%, #E43086 114.38%)
            box-shadow: 0px 0px 20px rgba(244, 100, 53, 4.35)                
            
    &__nav_row a
        color: #fff
        height: 88px
        width: 83px
        display: flex
        
        align-items: center
        justify-content: center
        
        font-family: 'Manrope', sans-serif
        font-style: normal
        font-weight: 500
        font-size: 18px

        transition: color 0.3s 
        
        gap: 15px
             
body
    background-color: #000

.taskDone
    opacity: 0.5
    cursor: default !important
    user-select: none

.plans
    display: flex
    flex-direction: column
    height: 100vh
    width: 100vw
    &__wrapper
        min-width: 886px
        height: 555px

        background: rgba(0, 0, 0, 0.2)
        border: 1px solid #272727
        border-radius: 20px

        padding: 40px

        display: flex
        gap: 46px

        & h1 
            font-family: 'Manrope'
            font-style: normal
            font-weight: 700
            font-size: 32px
            line-height: 44px
            
            text-align: left
            color: #FFFFFF
        & p
            text-align: left
            font-family: 'Manrope'
            font-style: normal
            // font-weight: 300
            font-size: 20px
            line-height: 27px
        &__right
            &__cards
                position: relative
                & img
                    position: absolute
        &__left
            &__sinup_steps
                display: flex
                gap: 32px
                flex-direction: column
                &__item
                    display: flex
                    gap: 16px
                    position: relative
                    transition: all 0.5s
                &__submit_btn
                    max-width: 322px
                    height: 49px

                    background: linear-gradient(267.74deg, #FFC700 5.99%, #E43086 114.38%)
                    box-shadow: 0px 0px 24px rgba(244, 100, 66, 0.35)
                    border-radius: 50px

                    border: none
                    
                    font-family: 'Manrope'
                    font-style: normal
                    font-weight: 700
                    font-size: 18px
                    line-height: 25px

                    color: #fff

                    cursor: pointer                                
                    transition: 0.5s all
        &__right
            position: relative
            top: 8px

.btn_gradient_animation
    background-size: 200% 200%
    animation: background 2s ease infinite
            
@keyframes background 
    0% 
        background-position: 0% 50%
    50%
        background-position: 100% 50%
    100%
        background-position: 0% 50%

.soon
    width: 40px
    height: 20px

    background: linear-gradient(297.55deg, rgba(228, 48, 134, 0.16) 13.57%, rgba(255, 199, 0, 0.16) 95%)
    border-radius: 26px    

    font-family: 'Manrope'
    font-style: normal
    font-weight: 600
    font-size: 11px
    line-height: 20px

    color: #FF9F2E

    display: flex
    align-items: center
    justify-content: center

.soon_link
    pointer-events: none
    user-select: none
    outline: none

.soon_link:focus
    outline: none


.soon_link p
    opacity: 0.3400000035762787
    
.sinup_steps__item__step_circle__item__wrapper                
    display: flex
    gap: 16px
    
.steps_btn
    width: 252px !important
    box-shadow: none !important
    height: 48px
    display: flex
    
.circle-active
    border: 1px solid rgba(105, 189, 7, 1)
    background: rgba(131, 255, 0, 0.11) !important
    
.inactive
    opacity: 0.4

.plans__wrapper__left__sinup_steps__item p
    font-family: 'Manrope'
    font-style: normal
    font-weight: 700
    font-size: 18px
    line-height: 25px
.plans__wrapper__left__sinup_steps__item__link_to
    width: 252px
    height: 48px
    border: none
    background: #4A99E9
    border-radius: 50px

    display: flex
    align-items: center
    justify-content: center
    gap: 8px
    transition: 0.3s all
    cursor: pointer
    & p
        font-family: 'Manrope'
        font-style: normal
        font-weight: 700
        font-size: 18px
        line-height: 25px

        margin: 0

.sinup_steps__item__step_circle
    display: flex
    align-items: center
    justify-content: center
    width: 50px
    height: 50px
    background: linear-gradient(143.32deg, #FFC700 -7.88%, #E43086 101.28%)
    // background-color: red
    border-radius: 30px
    // position: absolute
    z-index:  2
    &__innrer
        border-radius: 30px
        width: 48px
        height: 48px
        background-color: green
        background: linear-gradient(297.55deg, #13060c9a 13.57%, rgba(42, 34, 3, 0.3) 95%)
        position: absolute
        // z-index: -1

        display: flex
        align-items: center
        justify-content: center

        font-family: 'Inter', sans-serif
        font-style: normal
        font-weight: 700
        font-size: 16px
        line-height: 24px

        color: #fff

.fist_svg
    top: 164px
    left: 168px
.second_svg
    top: 65px
    left: 159px
.third_svg
    top: -15px
    left: 143px

.cards_circle 
    position: absolute

    width: 403px
    height: 403px

    background: linear-gradient(324.44deg, #FF9F2E -27.67%, #000000 28.75%), linear-gradient(343.54deg, #000000 5.18%, #D59851 221.13%)
    
    border-radius: 300px
    
    z-index: -1

    top: 133px
    left: 31px

.install_ext
    display: flex
    align-items: center
    justify-content: center
    flex-direction: column

.install_ext__container h1, h2, p
    color: #fff

.install_ext__supp_browsers__item
    display: flex
    align-items: center
    justify-content: center
    flex-direction: column
    gap: 14px
    

.install_ext__container
    & h1
        font-family: 'Manrope', serif
        font-size: 42px
        font-weight: 700
        margin-bottom: 30px
    & h2 
        font-family: 'Manrope', serif
        font-size: 32px
        font-weight: 700
        margin-bottom: 30px
    & img
        width: 50px
        height: 50px
    & a
        display: inline-flex
        margin-bottom: 30px

.install_ext__supp_browsers
    display: flex
    justify-content: space-between

.header_mobile
    display: none

.mobile
    display: none

@media (max-width: 992px)

    .desctope
        display: none

    [class*="__container"]
        // max-width: 375px
        margin: 0 auto
        padding: 0 15px

    .header
        display: none
    .mobile
        display: block
    
    .mobile-btn
        max-width: 322px !important
    

    .header_mobile
        display: flex
        flex-direction: row
        align-items: center            

        height: 80px
        padding: 0px 20px

        margin-bottom: 32px
        border-bottom: solid 1px rgba(39, 39, 39, 1)                
    .header_mobile__row
        width: 100vw
        display: flex
        align-items: center
        justify-content: space-between        
        &__menu
            display: flex
            align-items: center
            gap: 16px
        & button
            font-size: 18px
            font-family: Manrope
            font-weight: 700
    .header__nav_row__connect-btn
        display: inline-flex


    .plans_mobile    
        &__wrapper
            // width: 80vw
            min-height: 496px

            border: solid 1px rgba(39, 39, 39, 1)
            border-radius: 20px

            padding: 12px
            &__left
                text-align: left
                & h1
                    color: #FFF
                    font-size: 32px
                    font-family: Manrope
                    font-weight: 700
                    line-height: 33px
                & p
                    color: #FFF
                    font-size: 16px
                    font-family: Manrope
                    font-weight: 500
                    opacity: 0.800000011920929

// // PLANS PAGE SASS

.card

    width: 100vw
    height: 89vh
    position: relative
    &__container
        position: relative
        &__wrapper
            display: flex
            width: 328px
            position: relative
            padding: 32px
            flex-direction: column
            align-items: center            
            flex-shrink: 0
            gap: 32px
            border-radius: 20px
            border: 1px solid #272727
            &__payment_section
                position: relative
                display: flex
                flex-direction: column
                gap: 24px 
                &__text
                    display: flex
                    flex-direction: column
                    gap: 8px
                    & h2
                        font-size: 24px
                        font-family: Manrope
                        font-style: normal
                        font-weight: 700        
                    & p
                        font-size: 18px
                        font-family: Manrope
                        font-style: normal
                        font-weight: 300
                        opacity: 0.800000011920929
                &__price
                    display: flex
                    align-items: center

                    & > h2
                        font-size: 24px
                        font-family: Manrope
                        font-style: normal
                        font-weight: 700
                    &:nth-child(2)
                        font-size: 14px
                        font-family: Manrope
                        font-style: normal
                        font-weight: 400
                        color: #B5B5B5



.vpn
    &__container
        &__wrapper
            display: flex
            width: 328px
            padding: 32px
            flex-direction: column
            align-items: center
            gap: 24px

            border-radius: 20px
            border: 1px solid #272727
            background: #000
            box-sizing: border-box
            & h3
                color: #FFF
                text-align: center
                font-size: 16px
                font-family: Manrope
                font-style: normal
                font-weight: 500
                line-height: normal
        
    &__server
        display: flex
        flex-direction: column
        align-items: flex-start
        gap: 8px
        box-sizing: border-box
        &__selection
            width: 264px
            height: 48px
            border-radius: 36px
            background: rgba(255, 255, 255, 0.06)
            padding: 0px 16px 0px 16px
            font-size: 16px
            font-family: 'Inter', sans-serif
            font-style: normal
            font-weight: 500
            line-height: 24px

            outline: none
            border: none    
            color: #fff
            
            box-sizing: border-box
            // -webkit-appearance: none
            // -moz-appearance: none
            // appearance: none
            cursor: pointer
            transition: 0.5s all
            &__item
                font-size: 16px
                font-family: 'Inter', sans-serif
                font-style: normal
                font-weight: 500
                line-height: 24px
                color: #fff
        &__error
            display: flex
            flex-direction: column
            align-items: center
            justify-content: center
        
            text-align: center
            &__links
                display: flex
                flex-direction: column
                gap: 12px


.drop_down_plans_wrapper
    transition: 0.5s all

.vpn_ready_cert
    margin-top: 20px
    display: flex
    flex-direction: column
    gap: 24px
    & img
        border-radius: 15px
    &__data
        display: flex
        flex-direction: column
        gap: 12px



.air_drop_input
    box-sizing: border-box
    border: none
    min-width: 256px
    border-radius: 52px
    background: rgba(255, 255, 255, 0.06)   
    height: 48px
    padding: 0px 16px
    color: #fff
    

    font-family: Inter
    font-size: 16px
    font-style: normal
    font-weight: 500
    line-height: 24px
    // transition: 05s all
    &:focus
        outline: none
        border: 1px solid rgba(25, 202, 205, 0.3)
        
    &:active
        border: 1px solid rgba(25, 202, 205, 0.3)
        
        outline: none


.form_btn_inactive
    user-select: none
    // background-color: rgba(33, 127, 193, 0.628)
    opacity: 0.4
    cursor: default !important



.css-selector
    background: linear-gradient(270deg, #4A99E9, #28AC95, #0d7cec)
    background-size: 400% 400%

    -webkit-animation: AnimationName 2s ease infinite
    animation: AnimationName 2s ease infinite



@keyframes AnimationName 
    0%
        background-position:0% 50%
    50% 
        background-position:100% 50%
    100%
        background-position:0% 50%
